*, *:before, *:after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  position: relative;
  background: #EEE;
}

.gameboard {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 600px;
  height: 600px;
  outline: 1rem solid #FFF;
  background: #FFF;
}

.tile-column {
  position: relative;
  display: inline-block;
  width: 10%;
  height: 100%;
}

.tile {
  position: relative;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  background: #CCC;
}